body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  color: #fff;
  background-color: #282c34;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}

.Token {
  position: absolute;
}



.battle-view {
  width: 100vw;
  height: 100vh;
}

.battlemap-container {
  float: left;
  height: 99vh;
  width: 78vw;
  border: 1px solid gray;
  position: relative;
}

.battlemap {
  height: 99vh;
  width: 78vw;
  border: 1px solid gray;
}

.event-container {
  float: right;
  width: 21vw;
  height: 100vh;
}

.player-interface {
  height: 20vh;
}

.pass-turn {
  color: red;
  background-color: red;
  width: 100% !important;
}

.player-interface > button {
  width: 50%;
  height: 50%;
}

.dm-interface {
  height: 20vh;
}

.dm-interface > button {
  width: 50%;
  height: 35%;
}

.event-log {
  height: 60vh;
  border: 1px solid #000;
  overflow-y: scroll;
}

.heart {
  height: 1vh;
}

